<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="容器名称" prop="service_container_name">
                <el-input v-model="searchFormData.service_container_name" placeholder="请输入容器名称"></el-input>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[13, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)" >
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column fixed="left" label="执行操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="重载" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chongzai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="重启" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chongqi"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip v-if="scope.row.service_container_state_code_id === '1'" class="item" effect="dark" content="停止" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-tingzhi"></i></el-button>
                        </el-tooltip>
                        <el-tooltip v-else class="item" effect="dark" content="启动" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-qidong"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>
            <el-table-column
                    :formatter="format_service_container_state"
                    prop="service_container_state_code_id"
                    label="运行状态"
                    width="100">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="belong_to_server_id"
                    label="所属服务器id"
                    width="280">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="service_container_name"
                    label="容器名称"
                    width="160">
            </el-table-column>
            <el-table-column
                    prop="service_container_port"
                    label="容器端口"
                    width="80">
            </el-table-column>
            <el-table-column
                    :formatter="format_service_container_type"
                    prop="service_container_type_code_id"
                    label="容器类型"
                    width="120">
            </el-table-column>

            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="service_container_path"
                    label="容器路径">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="查看" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="修改" placement="left">
                            <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="8">
                        <el-tooltip class="item" effect="dark" content="删除" placement="left">
                            <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'serviceContainerConfList',
        data() {
            return {
                module_name: 'serviceContainerConf',
                refresh_page_method: 'refresh_' + this.module_name,
                serverId: '',
                service_container_type_code_list: null,
                service_container_state_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    service_container_name: '',
                    createTime: '',
                    order: '+_id'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.serverId = this.$route.params.serverId;
            this.service_container_type_code_list = await this.$setCodeList(this.service_container_type_code_list, 'service_container_type'); // 服务容器类型
            this.service_container_state_code_list = await this.$setCodeList(this.service_container_state_code_list, 'service_container_state'); // 服务容器类型
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_service_container_type(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.service_container_type_code_list, 'service_container_type_code_id')
            },
            format_service_container_state(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.service_container_state_code_list, 'service_container_state_code_id')
            },
            async r_search_list(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_service_container_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async r_delete_info(rowData) // 删除项目信息
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_service_container_info');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            search() {
                this.searchFormData.belong_to_server_id = this.$route.params.serverId;
                let searchData = this.searchFormData;
                this.r_search_list(searchData)
            },
            onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.r_delete_info(rowData)) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



