<template>
    <el-dialog title="服务容器信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="容器类型" prop="service_container_type_code_id">
                <el-select v-model="pageData.service_container_type_code_id" placeholder="请选择容器类型"  clearable>
                    <el-option v-for="item in service_container_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="容器名称" prop="service_container_name">
                <el-input v-model="pageData.service_container_name"></el-input>
            </el-form-item>
            <el-form-item label="容器端口" prop="service_container_port">
                <el-input v-model="pageData.service_container_port"></el-input>
            </el-form-item>
            <el-form-item label="容器用户" prop="service_container_user">
                <el-input v-model="pageData.service_container_user"></el-input>
            </el-form-item>
            <el-form-item label="容器密码" prop="service_container_password">
                <el-input v-model="pageData.service_container_password"></el-input>
            </el-form-item>
            <el-form-item label="容器路径" prop="service_container_path">
                <el-input v-model="pageData.service_container_path" style="width:696px" ></el-input>
            </el-form-item>
            <el-form-item label="启动容器命令" prop="start_container_command">
                <el-input v-model="pageData.start_container_command" style="width:696px" ></el-input>
            </el-form-item>
            <el-form-item label="关闭容器命令" prop="stop_container_command">
                <el-input v-model="pageData.stop_container_command" style="width:696px" ></el-input>
            </el-form-item>
            <el-form-item label="重载容器命令" prop="reload_container_command">
                <el-input v-model="pageData.reload_container_command" style="width:696px" ></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel == 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel == 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'serviceContainerConfInfo',
        data() {
            return {
                module_name: 'serviceContainerConf',
                refresh_page_method: 'refresh_' + this.module_name,
                service_container_type_code_list:null,
                pageData: {
                    belong_to_server_id: '', //所属服务器id
                    service_container_name: '', //服务容器名称
                    service_container_type_code_id: '', //服务容器类型
                    service_container_path: '', // 服务容器路径
                    service_container_port: '', // 服务容器端口
                    service_container_user: '', // 服务容器用户
                    service_container_password: '', // 服务容器密码
                    start_container_command: '', // 启动服务容器命令
                    stop_container_command: '', // 关闭服务容器命令
                    reload_container_command: '', // 重载服务容器命令
                    remark: '' // 备注
                },
                pageDataRules: {
                    service_container_type_code_id: [{required: true, message: '请选择容器类型', trigger: 'blur'}],
                    service_container_name: [{required: true, message: '请输入容器名称', trigger: 'blur'}]
                }
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name)
            }
        },
        async mounted() {
            this.service_container_type_code_list = await this.$setCodeList(this.service_container_type_code_list, 'service_container_type'); // 服务容器类型
        },
        methods: {
            async r_insert_info(item) // 插入信息
            {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_insert_service_container_info');
                if (data != null && data !== 'null')
                    return data.data;
            },
            async r_update_info(item) // 修改信息
            {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_update_service_container_info');
                if (data != null && data !== 'null')
                    return data;
            },
            onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        this.pageData.belong_to_server_id = this.$route.params.serverId;
                        let item = this.pageData;
                        let res = await _this.r_insert_info(item);
                        if (res != null) {
                            _this.$message.success('新增服务容器信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增服务容器信息失败');
                        }
                    }
                });
            },
            onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        this.pageData.belong_to_server_id = this.$route.params.serverId;
                        let item = this.pageData;
                        let res = await _this.r_update_info(item);
                        if (res != null) {
                            _this.$message.success('修改服务容器信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改服务容器信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>

    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


